@import url('https://fonts.googleapis.com/css?family=Roboto:700');

body {
  background: linear-gradient(180deg, #111983, #1c1f21, #00bfff, #111983, #1c1f21); 
  background-size: 400% 400%;
  font-family: sans-serif;
}

* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

#logo {
  width: 300px;
}
/* Style for the navigation links */
.navbar-links {
  list-style-type: none;
  margin-left: auto; /* Pushes the links to the right */
  display: flex;
  gap: 20px; /* Adds spacing between the items */
}

.navbar-links li {
  display: inline-block;
}

.nav-link {
  text-decoration: none; /* Removes text underline */
  color: #fff; /* Change the text color (optional) */
  padding: 10px; /* Adds padding for better click area */
}

.nav-link:hover {
  color: #ddd; /* Changes the color on hover (optional) */
}

#avatar {
  border-radius: 20px;
  width: 100%;
}
.basicInfo {
  display: flex;
  width: 800px;
  padding-top: 50px;
}

.resume{
background: white;
}

.trainings {
  p {
    padding: 10px;
  }
}

i:hover {
  text-decoration: underline;
}

.hello {
  text-shadow: 0 0 7px rgba(255,255,255,.3), 0 0 3px rgba(255,255,255,.3);
}

.basic {
  color: #e5e5e5;
  font-size: 2.26rem;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
}

.animation {
  height:50px;
  overflow:hidden;
  margin-left: 1rem;
  text-transform: uppercase;
}

.animation > div > div {
  padding: 0.25rem 0.75rem;
  height:2.81rem;
  margin-bottom: 2.81rem;
  display:inline-block;
}

.animation div:first-child {
  animation: text-animation 8s infinite;
}

.first div {
  font-weight: 600;
}
.second div {
  font-weight: 600;
}
.third div {
  font-weight: 600;
}

@keyframes text-animation {
  0% {margin-top: 0;}
  10% {margin-top: 0;}
  20% {margin-top: -5.62rem;}
  30% {margin-top: -5.62rem;}
  40% {margin-top: -11.24rem;}
  60% {margin-top: -11.24rem;}
  70% {margin-top: -5.62rem;}
  80% {margin-top: -5.62rem;}
  90% {margin-top: 0;}
  100% {margin-top: 0;}
}

.footer {
  padding: 20px;
  background-color: #ced4da;
  text-align: left;
  li {
    list-style: none;
  }
  #logo2 {
    width: 200px;
    padding-left: 30px;
    
  }
}

.page-link {
  border: none;
  margin: 10px;
}

.logosec {
  img {
    padding: 5px;
    width: 75px;
  }
}

.resume {
  padding: 40px 70px 40px 70px;
  h4 {
    font-weight: 600;
  }
  h2 {
    font-weight: 600;
  }
}

.WorkExp {
  li {
    font-style: italic;
  }
}

.menu {
  text-align: right;
  padding: 40px 40px 0 0;
}

.gallery {
  img {
    width: 100%;
  }
}
.hiddenMenu {
  display: none;
}


@media (max-width: 768px) {
.menu {
  display: none;
}
.hiddenMenu{
  display: block !important;
  text-align: right;
  button {
    width: 120px;
    border-radius: 30px;
    text-align: center;
  }
  padding-top: 30px;
}

.basic > .row {
  display: block;
  .col-3 {
    width: 100%;
    padding-top: none !important;
  }
}
.col-5 {
  width: 100%;
}
.col-5 > .basicInfo {
  display: block;
  text-align: center;
  width: 100%;
  .hello, .animation {
    width: 100%;
  }
}
.pt-4 {
  padding: 0 !important;
}
.ProjExperience {
  display: block !important;
}
.education {
  display: block !important;
}
.gallery > .row {
  display: block;
  .col-9, .col-3 {
    width: 100%;
  }
}
}